#view {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;	
}

#paths {
	position: fixed;
	bottom: 10px;
	right: 10px;
	z-index: 1;
	background: rgba(0,0,0,0.5);
	color: white;
	font-family: monospace;
	font-size: 13px;
	padding: 15px;
}

#msg {
	transition: opacity 0.3s;
	border-radius: 3px;
	position: fixed;
	bottom: 15px;
	left: 50%;
	transform: translate(-50%, 0);
	padding: 10px 25px;
	border: 1px solid;
	z-index: 2;
	font-family: sans-serif;
	font-size: 14px;
	opacity: 0;
}

#msg.show {
	opacity: 1;
}

#msg.error {
	background: #ff6465;
	color: #8c3736;
	border-color: #ad4444;
}

#msg.success {
	background: #84d2ab;
	color: #2a503e;
	border-color: #64a083;
}

#view > canvas {
	width: 100%;
	height: 100%;
}

img.debug,
canvas.debug {
	position: absolute;
	top: 15px;
	right: 15px;
	box-shadow: inset 0 0 0 1px red;
}

div.debug {
	position: absolute;
	top: 10px;
	right: 10px;
	background: rgba(0, 0, 0, 0.8);
	color: white;
	font-family: monospace;
	padding: 8px;
	white-space: pre;
}

#track-scrubber {
	background: rgba(0,0,0,0.5);
	position: absolute;
	bottom: 5px;
	left: 50%;
	transform: translate(-50%, 0);
	width: 50%;
	z-index: 10;
}

#fps-display {
	bottom: 0;
	top: auto !important;
}

#fps-display,
#paths,
#track-scrubber {
	opacity: 0.01 !important;
	transition: opacity 0.3s;
}

#fps-display:hover,
#paths:hover, #paths:focus,
#track-scrubber:hover {
	opacity: 1 !important;
}